import { fetchPosts } from "@/features/posts/post.server";

export default async function PostsPage() {
  const posts = await fetchPosts();

  return (
    <div className="max-w-7xl mx-auto p-6">
      <h1 className="text-3xl font-bold text-center mb-6">みんなの学習メモ</h1>

      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6">
        {videos.map((video) => (
          <a
            key={video.id}
            href={`/videos/${video.id}`}
            className="group rounded-xl overflow-hidden border border-gray-200 bg-white shadow-sm hover:shadow-xl transition-all duration-300"
          >
            <div className="w-full aspect-video overflow-hidden bg-gray-100">
              <img
                src={video.thumbnailUrl}
                alt={video.title}
                className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-300"
              />
            </div>

            <div className="p-4 space-y-3">
              <h2 className="text-lg font-semibold line-clamp-2 group-hover:text-blue-600 transition-colors">
                {video.title}
              </h2>

              <p className="text-sm text-gray-600">{video.authorName}</p>

              <div className="flex items-center gap-3 mt-2">
                <span className="text-sm text-gray-800">{video.posts[0].user.name}</span>
              </div>

              <p className="text-sm text-gray-700 line-clamp-2">{video.posts[0].text}</p>

              <p className="text-xs text-gray-400">
                {new Date(video.createdAt).toLocaleDateString('ja-JP')}
              </p>
            </div>
          </a>
        ))}
      </div>
    </div>
  );
}
